.content {
  padding: 5%;
  font-family: "Times New Roman", Times, serif;
}

.menu {
  text-align: center;
}

.title {
  font-size: 200%;
  text-align: center;
  font-weight: 900;
}

.section {
  font-size: 120%;
  text-align: center;
}

.small_title {
  padding: 1%;
  font-size: 150%;

  position: relative;
  font-size: 30px;
  z-index: 1;
  overflow: hidden;
  text-align: center;

  &:after,
  &:before {
    position: absolute;
    top: 51%;
    overflow: hidden;
    width: 48%;
    height: 1px;
    content: "\a0";
    background-color: #cccccc;
    margin-left: 2%;
  }

  &:before {
    margin-left: -50%;
    text-align: right;
  }

  // &:before {
  //   padding: 50px 0;
  // }
}

.Img_box {
  padding: 3%;
}

.video_box {
  padding: 3%;
}

.flex_box {
  padding: 2%;
  border-style: solid;
  margin: 2%;
  border-radius: 5px;
}

.icon {
  font-size: 20px;
  color: lightgray;
  padding: 2%;
}
